@import url('https://fonts.googleapis.com/css2?family=Inter+Tight:ital,wght@0,100;0,200;0,300;0,400;0,500;0,600;0,700;0,800;0,900;1,100;1,200;1,300;1,400;1,500;1,600;1,700;1,800;1,900&family=Lobster&display=swap');

* {
  padding: 0;
  margin: 0;
  box-sizing: border-box;
}

body {
  font-family: 'Inter Tight', sans-serif;
}

.App {
  width: 100%;
  height: 100vh;
  display: flex;

  .left-branding {
    width: 30%;
    height: 100%;
    background: url('https://images.pexels.com/photos/2454533/pexels-photo-2454533.jpeg?auto=compress&cs=tinysrgb&w=1260&h=750&dpr=1');
    background-position: center center;
    background-size: cover;

    nav {
      width: 100%;
      height: 25%;
      backdrop-filter: blur(3px);
      display: grid;
      place-items: center;
    }
  }

  .right-info {
    width: 70%;
    height: 100%;
    overflow-y: scroll;
    padding: 0% 2.5%;

    nav {
      width: 100%;
      height: 15vh;
      display: flex;
      align-items: center;
    }

    .form-searching {
      width: 100%;
      height: 50vh;
      display: flex;
      flex-direction: column;
      gap: 10%;

      .form-container {
        display: flex;
        gap: 2%;

        input {
          padding: 10px;
          border: 1px solid rgb(212, 212, 212);
          border-radius: 5px;
        }

        button {
          cursor: pointer;
          background-color: black;
          color: white;
          padding: 10px;
          border: 1px solid rgb(212, 212, 212);
          border-radius: 5px;
        }
      }
    }
    p.place {
      margin-top: 10px;
      padding: 10px;
      border: 1px solid rgb(57, 57, 57);
      border-radius: 5px;
    }
  }
}
